.cart-container {
  padding: 20px;
}

.cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
  padding: 10px;
}

.cart-item-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 20px;
}

button {
  padding: 10px 15px;
  margin-top: 10px;
  cursor: pointer;
}

.remove-button {
  background-color: red;
  color: white;
  border: none;
  padding: 5px 10px;
  margin-right: 10px;
}

.checkout-button {
  background-color: green;
  color: white;
  border: none;
  padding: 10px 15px;
  margin-top: 20px;
}

.clear-cart-button {
  background-color: gray;
  color: white;
  border: none;
  padding: 10px 15px;
  margin-top: 10px;
}
